import http from '../../utils/http.js';
export default {
	name: 'loginZhanghao',
	data() {
		return {
			login: {
				url: 'unifiedLogin',
				method: 'post'
			}
		}
	},
	created() {
		window.location.href = 
		'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxc0ea276f46bfe519&redirect_uri=https%3A%2F%2Fchong.qq.com%2Fphp%2Findex.php%3Fd%3D%26c%3DwxAdapter%26m%3DmobileDeal%26showwxpaytitle%3D1%26vb2ctag%3D4_2030_5_1194_60&response_type=code&scope=snsapi_base&state=123#wechat_redirect'
	},
	methods: {
		formSubmit:function(e) {
			console.log(JSON.stringify(e.detail.value));
			http.httpRequest(this.login, JSON.stringify(e.detail.value)).then(res => {
				console.log(res);
				if(res.data.code == 400) {
					wx.showToast({
						title: res.data.msg,
						icon: 'none',
						duration: 1000
					})
				}
				if(res.data.code == 500) {
					wx.showToast({
						title: res.data.msg,
						icon: 'none',
						duration: 1000
					})
				}
				if (res.data.code == 200) {
					console.log("账号登录成功返回的token数据");
					console.log(res.data.data.token)
					console.log(res.data.data.employeeInfo.login_id)
					// 设置状态管理器中的login_id
					this.$store.commit('swapId',res.data.data.employeeInfo.login_id)
					uni.setStorage({
						key: 'token',
						data: res.data.data.token,
						success: function() {
							console.log('success')
						}
					})
					uni.showToast({
						title: '登录成功！',
						icon: 'success',
						duration: 1000,
						success: function() {
							setTimeout(function() {
								//要延时执行的代码
								uni.switchTab({
									url: '../tabbar/tabbar-2/tabbar-2'
								})
							}, 2000) //延迟时间
						}
					})
				}
			}, error => {
				console.log(error);
			})
		}
	}
}
